.task{
  display: flex;
  background-color: var(--tg-theme-secondary-bg-color);


  img{
    min-width: 40px;
    min-height: 40px;
    max-width: 40px;
    max-height: 40px;
    background-color: #404040;
    border-radius: 50%;
    margin: 5px 5px 0 0;
  }
  .task-inner{
    display: flex;
    margin: 5px 0 10px 0;
    max-width: 80%;
    align-items: center;
    height: fit-content;
    background-color: #fff;
    border-radius: 15px;
    box-sizing: border-box;
    padding: 5px 5px;


    input{
      min-width: 25px;
      min-height: 25px;
      border-radius: 5px;
      background-color: #404040;
    }
    p{
      height: auto;
      width: 90%;
      text-wrap: normal;
      text-align: start;
      margin: 0;

    }
  }
}